import { mapGetters } from 'vuex';

const mixin = {
  data() {
    return {
      table: [
        { index: 0, name: '', label: '', width: 7, align: 'left' },
        { index: 1, name: this.$t('file.name'), label: '', width: 28, align: 'left' },
        { index: 2, name: this.$t('file.source'), label: '', width: 10, align: 'left' },
        { index: 3, name: this.$t('file.size'), label: '', width: 15, align: 'center' },
        { index: 4, name: this.$t('file.time'), label: '', width: 15, align: 'center' },
        { index: 5, name: this.$t('file.operation'), label: '', width: 25, align: 'center' },
      ],
      sortTypeObject: {
        3: 'size',
        4: 'time'
      },
      sortRuleObject: {
        1: 'asc',
        0: 'desc'
      },
      decryptFilePath: String // 
    }
  },
  computed: {
    ...mapGetters([ 'user_filePath' ])
  },
  methods: {
    /**
     * 
     * @param {*} el - dom
     * @returns - /
     */
    isScrollToBottom(el) {
      let [scrollTop, offsetHeight, scrollHeight] = [el.scrollTop, el.offsetHeight, el.scrollHeight];
      return !(scrollTop + offsetHeight < scrollHeight)
    },
    /**
     * openFileOrDir  osx
     * @param {String} path  
     * @param {Number} operType   1  2 
     */
    openFileOrDir(arg) {
      return new Promise(resolve => {
        let fileOperateParam = {
          filepath:  !this.$isOSX ? arg.path : arg.path.replace(/\(/ig, '\\(').replace(/\)/ig, '\\)'), //
          type: 7, //  0 1. 2. 3. 4. 6. 7.
          operType: arg.operType
        }
        console.log('fileOperateParam', fileOperateParam)
        this.$Service.file.fileOperate(fileOperateParam, openRes => {
          console.log('open file result =>>>>>', openRes);
          resolve(openRes)
        })
      }) 
    },
    /**
     * 
     */
    isNeedDecryptFile(file) {
      return new Promise((resolve) => {
        //   
        if (file.path === this.user_filePath + this.getFileName(file.url)) {
          if (!file.encrypt_key) {
            // 
            if (this.$Service.file.fileOperate({ type: 0, filepath: file.path})) {
              this.decryptFilePath = this.createCachePath(file.name);
              if ( decryptFilePath === 1) {
                resolve(1);
                return;
              }
              this.handleNoPasswordFile(file.path, this.decryptFilePath);
              resolve(this.decryptFilePath);
              return;
            }
            resolve(3);
            return;
          }
          // 
          if (!this.$Service.file.fileOperate({ type: 0, filepath: this.decryptFilePath})) {
            this.decryptFile(file).then(result => {
              resolve(result);
            })
          } else {
            resolve(this.decryptFilePath);
          }
        } else {
          if (!this.$Service.file.fileOperate({ type: 0, filepath: file.path})) {
            resolve(3);
          } else {
            resolve(file.path);
          }
        }
      })
    },
    /**
     * 
     */
    decryptFile(file) {
      return new Promise((resolve) => {
        if (this.$Service.file.fileOperate({ type: 0, filepath: file.path})) {
          let decryptFilePath = this.createCachePath(file.name);
          if ( decryptFilePath === 1) {
            resolve(1);
            return;
          }
          this.decrypted(file.encrypt_key, file.path, decryptFilePath, 2).then(decryptedRes => {
            if (decryptedRes.result) {
              resolve(decryptFilePath);
            } else {
              resolve(2);
            }
          })
        } else {
          resolve(3);
        }
      })
    },
    /**
     * 
     */
    createCachePath(fileName) {
      const cachePath = `${this.user_filePath}cache/`;
      let isCreateCache = this.$Service.file.fileOperate({
        filepath: cachePath,
        type: 1
      })
      console.log('cachePath', cachePath);
      console.log('isCreateCache', isCreateCache);
      if (isCreateCache) {
        console.log('');
        return cachePath + fileName.replace(/\//g, '_');
      } else{
        console.log('');
        return 1;
      }
    },
    /**
     * @param {String} encryptkey 
     * @param {String} srcpath 
     * @param {String} destpath 
     * @param {Number} flag   1   2 
     */
    decrypted(encryptkey, srcpath, destpath, flag = 1) {
      return new Promise((resolve, reject) => {
        let decryptFileParam = {
          encryptkey: encryptkey,
          srcpath: srcpath,
          destpath: destpath,
          flag: flag
        }
        console.log('decryptFileParam', decryptFileParam)
        this.$Service.file.decryptFile(decryptFileParam, resp => {
          console.log('decryptFileResult', resp);
          resolve(resp)
        })
      })
    },
     /**
     *  ()
     */
    getFileName(str) {
      let index = str.lastIndexOf('/');
      let path = str.substr(index + 1); // 
      let fileName = path.substr(0, path.lastIndexOf('.')); // ()
      return fileName || path; // 
    },
      /**
     * 
     * 
     * @param {String} srcpath 
     * @param {String} destpath 
     */
    handleNoPasswordFile(srcpath, destpath) {
      this.$fs.createReadStream(srcpath).pipe(this.$fs.createWriteStream(destpath));
    },
  }
};
export default mixin;