<template>
  <div id="app">
    <search-manager></search-manager>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import SearchManager from './components/Search/index';
export default {
  name: 'app',
  components: {
    SearchManager
  },
  computed: {
    ...mapGetters([ 'uploader_list' ])
  },
  created() {
    console.log('get imsdkservice: ', this)
    console.log('i get env: ', process.env.NODE_ENV);
    if (process.env.NODE_ENV === 'development') {
      let loginParam = {
        user: '008618792550841', //  ** 0086**
        pwd: '123123', // 
        userType: 1, // ,
        server: 'vrv', // ,
        nation: '0086' // ,
      };
      this.$Service.auth.login(loginParam, resp => {
        console.log('i get login result: ', resp);
        // 0  113 
        if (resp.code === 0 || resp.code === 113) {
          //  
          this.init(); // 
        }
      })
    }
    this.init(); // 
  },
  methods: {
    init() {
      // 
      const param = {
        targetId: '0',
        count: 1000
      }
      this.$Service.file.getFileList(param, resp => {
        console.log('getFileListresp=>>>>>>>>>', resp);
        if (resp.code === 0) {
          if (resp.fileInfos.length === 0) {
            return;
          }
          this.$store.commit('SET_FILELIST', resp.fileInfos);
        }
      })
      this.getFilePath();
      this.getAccountInfo();
    },
    // 
    getFilePath() {
      this.$Service.config.userFilePath(null, resp => {
        console.log(`user_filePath:::`, resp);
        this.$store.commit('SET_FILE_PATH', resp.path);
      });
    },
    // 
    getAccountInfo() {
      this.$Service.user.getAccountInfo(null, resp => {
        // this.$Service.log(`${JSON.stringify(resp)}`);
        this.$store.commit('SET_ACCOUNT', resp);
      })
    },
  }
}
</script>

<style lang="scss">
body,
html,
#app {
  width: 100%;
  height: 100%;
  font-family: '', 'Microsoft YaHei', Arial, Helvetica, sans-serif, '';
  overflow: hidden;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  background: transparent;
  position: relative;
}
* {
  &::-webkit-scrollbar {
    width : 6px;  /**/
  }
  &::-webkit-scrollbar-thumb {
    height: 5px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #C0C4CC;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #ededed;
  }
}
@import '~bulma/css/bulma.css';
</style>
