<template>
<div class="d-input-wrapper" :class="inputClass">
  <div class="d-input-box" :class="[
    outlined ? 'd-input-outline' : 'd-input-single-line',
    {
      'd-input-is-focused': isfocus,
      'd-input-is-disabled': disabled,
      'is-exceed': inputExceed
    }
  ]"
  >
  <!--  begin -->
    <div class="prefix-slot" v-if="$slots.prepend">
      <slot name="prepend"></slot>
    </div>
  <!--  end -->
  <!-- Icon begin -->
    <div class="slot slot-icon prefix-slot" v-if="$slots.prefix || prefixIcon">
      <slot name="prefix"></slot>
      <d-icon v-if="prefixIcon" :icon="prefixIcon" size="inherit"></d-icon>
    </div>
  <!-- Icon end -->
  <!--  begin -->
    <input
      class="d-input"
      :class="getInputClasses"
      ref="input"
      :name="name"
      :type="getType"
      :value="currentValue"
      :placeholder="placeholder"
      :disabled="disabled"
      :autofocus="autofocus"
      :readonly="readonly"
      :maxlength="maxlength"
      @input="handleInput"
      @focus="handleFocus"
      @blur="handleBlur"
      @keyup.enter.stop="handleEnter"
    >
  <!--  end -->
  <!-- Icon begin -->
    <div class="slot slot-icon suffix-slot" v-if="$slots.suffix || suffixIcon">
      <slot name="suffix"></slot>
      <d-icon v-if="suffixIcon" :icon="suffixIcon" size="inherit"></d-icon>
    </div>
  <!-- Icon end -->
  <!--  begin -->
    <div class="slot-icon password-icon" v-if="password && !passwordInvisible && value" @click.stop="handlePasswordVisible">
      <d-icon :icon="takePasswordVisible ? 'd-icon-invisible' : 'd-icon-eye'" @click.stop="handlePasswordVisible"  size="inherit"></d-icon>
    </div>
  <!--  end -->
  <!--  begin -->
    <div class="slot-icon clear-icon" v-if="clearable && value" @click.stop="handleClear">
      <d-icon icon="d-icon-close-button" @click.stop="handleClear" size="inherit"></d-icon>
    </div>
  <!--  end -->
  <!--  begin -->
    <div class="slot-icon search-icon" v-if="search && value" @click.stop="handleSearch">
      <d-icon :icon="getLoadingState.icon" @click.stop="handleSearch" :class="{ 'loading': getLoadingState.state }" size="inherit"></d-icon>
    </div>
  <!--  end -->
  <!--  begin -->
    <div class="count" v-if="isShowWordLimit">
      <span class="count-word">{{ currentLength }}/{{ maxlength }}</span>
    </div>
  <!--  end -->
  <!--  begin -->
    <div class="suffix-slot" v-if="$slots.append">
      <slot name="append"></slot>
    </div>
  <!--  end -->
  </div>
</div>
</template>

<script>
import DIcon from '../icon/Icon';

export default {
  name: 'd-input',
  components: {
    [DIcon.name]: DIcon
  },
  inheritAttrs: false,
  props: {
    name: String,
    value: [String, Number],
    // 
    outlined: {
      type: Boolean,
      default: false
    },
    // 
    placeholder: {
      type: String,
      default: ''
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // 
    size: {
      type: String,
      default: 'default',
      validator(val) {
        return val && ['small', 'default', 'medium', 'large'].includes(val);
      }
    },
    // 
    clearable: {
      type: Boolean,
      default: false
    },
    // 
    autofocus: {
      type: Boolean,
      default: false
    },
    // 
    readonly: {
      type: Boolean,
      default: false
    },
    // 
    password: {
      type: Boolean,
      default: false
    },
    // 
    passwordInvisible: {
      type: Boolean,
      default: false
    },
    // 
    search: {
      type: Boolean,
      default: false
    },
    // Icon
    prefixIcon: {
      type: String,
      default: ''
    },
    // Icon
    suffixIcon: {
      type: String,
      default: ''
    },
    // 
    showWordLimit: {
      type: Boolean,
      default: false
    },
    // 
    finishSearch: {
      type: Boolean,
      default: true
    },
    // 
    maxlength: {
      type: String,
      validator(val) {
        return val && (Number(val) > 0);
      }
    }
  },
  data() {
    return {
      currentValue: this.value,
      isfocus: false,
      takePasswordVisible: false,
      loading: false
    };
  },
  computed: {
    inputClass() {
      let classList = [];
      // 
      classList.push(`d-input-is-${this.size}`);
      return classList;
    },
    /**
     * 
     */
    getType() {
      if (this.password) {
        return this.takePasswordVisible ? 'text' : 'password';
      }
      return 'text';
    },
    /**
     * 
     */
    isShowWordLimit() {
      return (Number(this.maxlength) > 0) && this.showWordLimit;
    },
    /**
     * 
     */
    currentLength() {
      return this.value.length;
    },
    /**
     * 
     */
    inputExceed() {
      return this.showWordLimit && (this.currentLength > this.maxlength);
    },
    /**
     * 
     */
    getLoadingState() {
      if (this.loading) {
        return { icon: 'd-icon-loading-round', state: true };
      }
      return { icon: 'd-icon-search', state: false };
    },
    /** 
     * input
     */
    getInputClasses() {
      return [ 'd-input', 
        { 'no-right-padding': this.$slots.append || this.$slots.suffix || this.suffixIcon || this.search || (this.password && !this.passwordInvisible) || this.clearable,
        'no-left-padding': this.$slots.prepend || this.$slots.prefix || this.prefixIcon}
      ]
    }
  },
  watch: {
    value(val) {
      this.setCurrentValue(val);
    },
    finishSearch(val) {
      this.loading = !val;
    }
  },
  methods: {
    /**
     * 
     */
    setCurrentValue(val) {
      if (val === this.currentValue) {
        return;
      }
      this.currentValue = val;
    },
    /**
     * 
     */
    handleFocus(event) {
      this.isfocus = true;
      this.$emit('focus', event);
    },
    /**
     * 
     */
    handleBlur(event) {
      this.isfocus = false;
      this.$emit('blur', event);
    },
    /**
     * 
     */
    handleClear() {
      this.setCurrentValue('');
      this.$emit('input', ''); // input,
      this.$emit('change', ''); // change
      this.$emit('clear'); // ()
    },
    /**
     * 
     */
    handleInput(event) {
      let val = event.target.value;
      this.setCurrentValue(val);
      this.$emit('input', val); // c
      this.$emit('change', val);
    },
    /**
     * 
     */
    handlePasswordVisible() {
      this.takePasswordVisible = !this.takePasswordVisible;
    },
    /**
     * 
     */
    handleSearch() {
      if (this.loading) {
        return;
      }
      this.loading = true;
      this.$emit('search', this.currentValue);
    },
    /**
     * 
     */
    handleEnter(event) {
      this.$emit('enter', event);
      if (this.search) {
        this.$emit('search', this.currentValue);
      }
    },
    /**
     * 
     */
    focus() {
      this.isfocus = true;
      this.$nextTick(() => {
        this.$refs.input.focus();  
      })
    },
    /**
     * 
     */
    blur() {
      this.isfocus = false;
      this.$refs.input.blur();  
    }
  },
};

</script>
