<template>
  <transition-group :tag="tagName" name="skeleton" style="position: relative;width: 100%;height: 100%"
    @before-enter="(el) => $emit('before-enter', el)"
    @before-leave="(el) => $emit('before-leave', el)"
    @after-enter="(el) => $emit('after-enter', el)"
    @after-leave="(el) => $emit('after-leave', el)"
  >
    <div v-if="isInit" key="component" style="height: 100%" class="real_component">
      <slot :loading="loading"></slot>
    </div>
    <div v-else-if="$slots.skeleton" key="skeleton" class="skeleton_component">
      <slot name="skeleton"></slot>
    </div>
    <div v-else key="loading"></div>
  </transition-group>
</template>

<script>
export default {
  name: 'd-skeleton',

  props: {
    // 
    timeout: {
      type: Number
    },
    // 
    tagName: {
      type: String,
      default: 'div'
    },
    // 
    viewport: {
      type: typeof window !== 'undefined' ? window.HTMLElement : Object,
      default: () => null
    },
    // , css
    threshold: {
      type: String,
      default: '0px'
    },
    //  direction , verticalhorizontal
    direction: {
      type: String,
      default: 'vertical'
    },
    // 
    maxWaitingTime: {
      type: Number,
      default: 50
    }
  },

  data() {
    return {
      isInit: false,
      timer: null,
      io: null,
      loading: false
    };
  },

  created() {
    // timeouttimeout
    if (this.timeout) {
      this.timer = setTimeout(() => {
        this.init();
      }, this.timeout);
    }
  },

  mounted() {
    if (!this.timeout) {
      // 
      let rootMargin;
      switch (this.direction) {
        case 'vertical':
          rootMargin = `${this.threshold} 0px`;
          break;
        case 'horizontal':
          rootMargin = `0px ${this.threshold}`;
          break;
      }

      try {
        // 
        this.io = new window.IntersectionObserver(this.intersectionHandler, {
          rootMargin,
          root: this.viewport,
          threshold: [0, Number.MIN_VALUE, 0.01]
        });
        this.io.observe(this.$el);
      } catch (e) {
        this.init();
      }
    }
  },

  beforeDestroy() {
    // 
    if (this.io) {
      this.io.unobserve(this.$el);
    }
  },

  methods: {
    // 
    intersectionHandler(entries) {
      if (
        // 
        entries[0].isIntersecting
        // 0
        || entries[0].intersectionRatio
      ) {
        this.init();
        this.io.unobserve(this.$el);
      }
    },

    // 
    init() {
      // 
      this.$emit('beforeInit');
      this.$emit('before-init');

      // 
      this.loading = true;

      // 
      // requestAnimationFrame
      this.requestAnimationFrame(() => {
        this.isInit = true;
        this.$emit('init');
      });
    },

    requestAnimationFrame(callback) {
      // 
      // 
      setTimeout(() => {
        if (this.isInit) return;
        callback();
      }, this.maxWaitingTime);

      // requestAnimationFrame 
      return (window.requestAnimationFrame
      || (callback => setTimeout(callback, 1000 / 60)))(callback);
    }
  }
};
</script>
